@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1100px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 20px;

////////////////////
// フォント設定
////////////////////

$base-font-family: "Gotu", sans-serif;
$second-font-family: "Noto Serif JP", sans-serif;
$third-font-family: "Noto Sans JP", sans-serif;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $base-font-family;
}

// フォントウェイト
$regular: 300;
$normal: 400;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$green: #408f95;
$green2: #ddf0f1;
$white: #fff;
$brown: #796a64;
$black: #0d2936;
$gray: #f2f2f2;
$red: #c94800;
