@charset "UTF-8";
body {
  font-family: "Gotu", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.campaign {
  padding-top: 2.5rem;
  padding-bottom: 5rem;
  overflow: hidden;
  position: relative;
}
@media screen and (min-width: 768px) {
  .campaign {
    padding-top: 7.5rem;
    padding-bottom: 8.75rem;
  }
}

.campaign__cards {
  margin-top: 1.5rem;
}
@media screen and (min-width: 768px) {
  .campaign__cards {
    margin-top: 2.5rem;
  }
}

.campaign__contents {
  padding-top: 0.93rem;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 1.5rem;
}

.campaign__box {
  border-bottom: 1px solid #408f95;
}

.campaign__tag {
  border: 1px solid #408f95;
  display: inline-block;
  padding: 0.25rem 0.75rem;
}

.campaign__category {
  color: #408f95;
  font-size: 0.75rem;
  font-weight: 300;
  line-height: 1.88;
  font-family: "Noto Serif JP", sans-serif;
}

.campaign__description {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
}

.campaign__container {
  padding-top: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.campaign__price-wrap {
  padding-top: 0.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.campaign__price-out {
  font-size: 1.25rem;
  font-weight: 700;
  font-family: "Noto Sans JP", sans-serif;
  text-decoration: line-through red;
}

.campaign__price-in {
  padding-left: 1rem;
  font-size: 2rem;
  font-weight: 700;
  font-family: "Noto Sans JP", sans-serif;
  color: #408f95;
}

.campaign__contact {
  padding-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .campaign__contact {
    padding-top: 5rem;
  }
}

.swiper2 {
  margin-right: calc(70% - 50vw);
}

.campaign__card {
  box-shadow: 2px 2px 4px;
}
@media screen and (min-width: 768px) {
  .campaign__card {
    max-width: 20.8rem;
    width: 100%;
  }
}

.campaign__title {
  position: relative;
}

.campaign__wrap {
  display: flex;
  position: absolute;
  right: 23%;
  top: 18%;
}

/* 前へ次への矢印カスタマイズ */
/* デフォルトの矢印を非表示にする */
/* 前へ次への矢印カスタマイズ */
.swiper-button-prev,
.swiper-button-next {
  height: 5rem;
  width: 5rem;
}

/* 前へ次への矢印カスタマイズ */
.swiper-button-prev::after,
.swiper-button-next::after {
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
  height: 50px;
  margin: auto;
  width: 50px;
}

/* 前への矢印カスタマイズ */
.swiper-button-prev::after {
  background-image: url(/assets/images/common/campaign-prev.svg);
}

/* 次への矢印カスタマイズ */
.swiper-button-next::after {
  background-image: url(/assets/images/common/campaign-next.svg);
}

/* 画像サイズ調整 */
.swiper-slide img {
  height: auto;
  width: 100%;
}

.drawer-menu {
  position: fixed;
  height: 100vh;
  width: 100%;
  z-index: 1000;
  top: 0;
  right: -100%;
  opacity: 1;
  background-color: #408f95;
  transition: right 0.3s ease;
}

.drawer-menu.is-open {
  right: 0;
}

.drawer-menu__inner {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 2.5rem;
}

.drawer-menu__wrapper {
  display: flex;
}

.drawer-menu__body {
  margin-left: 0.5rem;
}

.drawer-menu__item {
  font-size: 1rem;
  font-weight: 300;
  color: #fff;
  display: inline-block;
  text-align: left;
  padding-top: 1.5rem;
}

a.drawer-menu__item {
  font-size: 1rem;
  font-weight: 700;
  font-family: "Gotu", sans-serif;
  position: relative;
  text-align: left;
  margin-top: 1rem;
}

a.drawer-menu__item::before {
  content: url("/assets/images/common/drawer-icon.svg");
  margin-right: 0.3rem;
  vertical-align: middle;
  position: absolute;
  top: 1.56rem;
  left: -1.25rem;
}

.header {
  background: rgba(13, 41, 54, 0.68);
  height: 5rem;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}
@media screen and (min-width: 768px) {
  .header {
    display: flex;
    align-items: center;
  }
}

.header__inner {
  display: flex;
  height: inherit;
  align-items: center;
  padding: 0 0.937rem;
}
@media screen and (min-width: 768px) {
  .header__inner {
    width: 100%;
  }
}

.header__logo {
  margin-right: auto;
  z-index: 1500;
}

.header-logo__link {
  width: 6.375rem;
}
@media screen and (min-width: 768px) {
  .header-logo__link {
    width: 8.312rem;
  }
}

.hamburger {
  position: fixed;
  z-index: 1500;
  top: 1.75rem;
  right: 1rem;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
}

.hamburger.is-active {
  top: 1.65rem;
  right: 1.25rem;
}

.hamburger span {
  position: absolute;
  left: 0;
  display: inline-block;
  width: 100%;
  height: 0.06rem;
  background-color: #fff;
}

.hamburger,
.hamburger span {
  display: inline-block;
  transition: all 0.5s;
  box-sizing: border-box;
}

.hamburger span:nth-child(1) {
  top: 0;
}

.hamburger span:nth-child(2) {
  top: 0.75rem;
}

.hamburger span:nth-child(3) {
  top: 1.5rem;
}

.hamburger.is-active span:nth-child(1) {
  -webkit-transform: translateY(0.76rem) rotate(45deg);
  transform: translateY(0.76rem) rotate(45deg);
}

.hamburger.is-active span:nth-child(2) {
  opacity: 0;
}

.hamburger.is-active span:nth-child(3) {
  -webkit-transform: translateY(-0.76rem) rotate(-45deg);
  transform: translateY(-0.76rem) rotate(-45deg);
}

.header__wrapper {
  height: inherit;
}

.header__nav {
  height: inherit;
}

.header__items {
  display: flex;
  align-items: center;
  justify-content: center;
  height: inherit;
}

.header__item {
  display: flex;
  height: inherit;
}

.header__item:not(:first-child) {
  margin-left: 2.5rem;
}

.header__link {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 1.125rem;
  color: #fff;
}

.header__link span {
  display: block;
  font-size: 0.6rem;
  position: relative;
}

.header__link span::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 10%;
  /*線の形状*/
  width: 80%;
  height: 0.125rem;
  background: #fff;
  /*アニメーションの指定*/
  transition: all 0.3s;
  transform: scale(0, 1); /*X方向0、Y方向1*/
  transform-origin: center top; /*上部中央基点*/
}

.header__link:hover span::after {
  transform: scale(1, 1);
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.mv {
  width: 100%;
}

.mv__inner {
  width: 100%;
  height: inherit;
  position: relative;
}

.mv__slider,
.swiper-slide picture,
.swiper-slide img {
  height: inherit;
}

.swiper-slide img {
  object-fit: cover;
}

.mv__title-wrap {
  position: absolute;
  z-index: 10;
  top: -5%;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 66%;
  height: 5rem;
  text-align: center;
}

.mv__title {
  font-size: 2.5rem;
  font-weight: 300;
  font-family: "Gotu", sans-serif;
  letter-spacing: 0.54em;
  color: #fff;
}
@media screen and (min-width: 768px) {
  .mv__title {
    font-size: 5rem;
  }
}

.mv__title-sub {
  font-size: 1.25rem;
  font-family: "Gotu", sans-serif;
  letter-spacing: 0.1rem;
  color: #fff;
}

.section-title {
  display: inline-block;
  width: 100%;
}

.section-title__main {
  color: #0d2936;
  font-family: "Gotu", sans-serif;
  font-size: 2rem;
  line-height: 1.25;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .section-title__main {
    font-size: 2.5rem;
  }
}

.section-title__main span {
  color: #408f95;
}

.section-title__sub {
  color: #408f95;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 0.75rem;
  font-weight: 300;
  line-height: 1.5;
  text-align: center;
}

a.btn {
  border: 1px solid #408f95;
  display: block;
  text-decoration: none;
  position: relative;
  width: 12.6rem;
  margin: auto;
  padding: 1rem 3rem;
  font-weight: bold;
  color: #fff;
  background: #408f95;
  transition: 0.3s ease-in-out;
}

a.btn::before {
  content: "";
  position: absolute;
  top: 9px;
  left: 10px;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  border: 1px solid #408f95;
  transition: 0.2s;
  z-index: -1;
}

.btn-icon {
  position: absolute;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
  width: 2.5rem;
}

a.btn:hover .btn-icon path {
  stroke: #408f95;
}

a.btn:hover {
  background: #fff;
  color: #408f95;
}

@media screen and (min-width: 768px) {
  .md-none {
    display: none;
  }
}

.md-show {
  display: none;
}
@media screen and (min-width: 768px) {
  .md-show {
    display: block;
  }
}